import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import classnames from 'classnames';
import { IUser } from '../../../types/user';

interface INav {
  logo?: string;
  albums?: any[];
  user?: IUser;
  urlPrefix?: string;
}
const Nav: React.FC<INav> = function ({
  logo = '',
  albums = [],
  user,
  urlPrefix = '',
}) {
  const [menuOpen, setOpen] = useState(false);

  function onMenuClick() {
    setOpen(!menuOpen);
  }

  React.useEffect(() => {
    let el: any = document.getElementsByTagName('html')[0];
    if (menuOpen) {
      el.classList.add('overflow-hidden');
    } else {
      if (el.classList.contains('overflow-hidden')) {
        el.classList.remove('overflow-hidden');
      }
    }
    return () => {
      if (el.classList.contains('overflow-hidden')) {
        el.classList.remove('overflow-hidden');
      }
    };
  }, [menuOpen]);

  return (
    <nav className="flex items-center pt-4 md:pt-8 px-5 md:px-16 sticky top-0 bg-white z-20">
      {!!logo && (
        <div className="pb-3">
          <img
            src={logo}
            alt="logo"
            className="w-10 h-10 md:w-16 md:h-16 rounded-full"
          />
        </div>
      )}

      <div className="ml-auto block md:hidden relative z-20">
        <button
          className={classnames(
            'hamburger hamburger--3dxy focus:outline-none',
            {
              'is-active': menuOpen,
            }
          )}
          type="button"
          onClick={onMenuClick}
        >
          <span className="hamburger-box">
            <span className="hamburger-inner"></span>
          </span>
        </button>
      </div>

      {menuOpen && (
        <div className="block md:hidden fixed top-0 left-0 w-full h-full pt-16 flex items-stretch z-10">
          <div className="pt-2 bg-white flex-grow text-center flex flex-col text-3xl font-light justify-center">
            <div className="mb-3">
              <NavLink
                to={`${urlPrefix}/gallery`}
                activeClassName="border-b border-gray-600"
                className="pb-1 "
                onClick={onMenuClick}
              >
                Gallery
              </NavLink>
            </div>

            {albums.map((album) => (
              <React.Fragment key={album._id}>
                {!album.default && (
                  <div className="mb-3">
                    <NavLink
                      to={`${urlPrefix}/album/${album._id}`}
                      activeClassName="border-b border-gray-600"
                      className="pb-1 "
                      onClick={onMenuClick}
                    >
                      {album.name}
                    </NavLink>
                  </div>
                )}
              </React.Fragment>
            ))}

            <div className="mb-3">
              <NavLink
                to={`${urlPrefix}/about`}
                activeClassName="border-b border-gray-600"
                className="pb-1 "
                onClick={onMenuClick}
              >
                About Me
              </NavLink>
            </div>
            <div className="mb-3">
              <NavLink
                to={`${urlPrefix}/contact`}
                activeClassName="border-b border-gray-600"
                className="pb-1 "
                onClick={onMenuClick}
              >
                Contact
              </NavLink>
            </div>

            <a
              href={`https://www.instagram.com/${user?.instaUsername}/`}
              target="__blank"
            >
              <i className="fab fa-instagram" />
            </a>
          </div>
        </div>
      )}

      <div className="ml-auto font-light hidden md:block">
        <NavLink
          to={`${urlPrefix}/gallery`}
          activeClassName="border-b border-gray-600"
          className="pb-1 ml-16"
        >
          Gallery
        </NavLink>

        {albums.map((album) => (
          <React.Fragment key={album._id}>
            {!album.default && (
              <NavLink
                to={`${urlPrefix}/album/${album._id}`}
                activeClassName="border-b border-gray-600"
                className="pb-1 ml-16"
              >
                {album.name}
              </NavLink>
            )}
          </React.Fragment>
        ))}

        <NavLink
          to={`${urlPrefix}/about`}
          activeClassName="border-b border-gray-600"
          className="pb-1 ml-16"
        >
          About Me
        </NavLink>
        <NavLink
          to={`${urlPrefix}/contact`}
          activeClassName="border-b border-gray-600"
          className="pb-1 ml-16"
        >
          Contact
        </NavLink>

        <a
          href={`https://www.instagram.com/${user?.instaUsername}/`}
          className="ml-16 text-lg"
          target="__blank"
        >
          <i className="fab fa-instagram" />
        </a>
      </div>
    </nav>
  );
};

export default Nav;
