/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { IReview } from '../../../types/review';
import TestimonialCard from '../TestimonialCard/TestimonialCard';
import ThemeContext from '../../theme.context';

interface TestimonialsProps {}

const Testimonials: React.FunctionComponent<TestimonialsProps> = () => {
  const { review } = React.useContext(ThemeContext);

  const [reviews, setReviews] = React.useState<IReview[]>([]);
  const [page, setPage] = React.useState<number>(0);

  React.useEffect(() => {
    setNextReviews();
  }, [review]);

  function setNextReviews() {
    if (review.items.length) {
      setReviews([...reviews, ...review.items.slice(page, page + 3)]);
      setPage(page + 3);
    }
  }

  return (
    <div className="bg-gray -mx-5 md:-mx-20 mt-4 md:mt-10">
      <div className="px-5 md:px-20 py-10">
        <header className="text-center mb-5">
          <h2 className="font-heading text-4xl">Testimonials</h2>
          <p className="font-light mt-5 mb-10">
            Reviews are independently collected by Nishi.app
          </p>
        </header>

        {/* testimonial cards */}
        <div className="md:flex mb-10 justify-center flex-wrap">
          {reviews?.map((testimonial) => (
            <div className="flex-none md:w-4/12 p-3" key={testimonial._id}>
              <TestimonialCard review={testimonial} />
            </div>
          ))}
        </div>

        {reviews.length < review.items?.length && (
          <div className="text-center">
            <button
              className="inline-block border-black border-2 px-8 py-4 hover:bg-black hover:text-white focus:outline-none"
              onClick={setNextReviews}
            >
              See More
            </button>
          </div>
        )}
      </div>
    </div>
  );
};
export default Testimonials;
