import React from 'react';
import './Collage.scss';

interface CollageProps {
  images?: string[];
}

const Collage: React.FunctionComponent<CollageProps> = ({ images = [] }) => {
  return (
    <div className="langtang-Collage">
      {images?.map((image: string, index: number) => (
        <img src={image} alt="hair and makeup artist melbourne" key={index} />
      ))}
    </div>
  );
};
export default Collage;
