import React from 'react';
import './TestimonialCard.scss';
import { IReview } from '../../../types/review';

interface TestimonialCardProps {
  review?: IReview;
}

const TestimonialCard: React.FunctionComponent<TestimonialCardProps> = ({
  review,
}) => {
  return (
    <div className="langtang-testimonial-card text-center mb-10 md:mb-0">
      {!!review?.images?.length && (
        <figure className="pt-full relative block mb-4">
          <img
            src={review?.images[0]}
            className=" absolute top-0 left-0 w-full h-full object-center object-cover"
            alt="testimonial"
          />
        </figure>
      )}

      <p className="my-4">{review?.comments}</p>
      <p className="mb-4">- {review?.customerName}</p>
      <p className="text-sm">Verified review by Nishi.app on {review?.date}</p>
    </div>
  );
};
export default TestimonialCard;
