import React from 'react';
import { IUser } from '../types/user';
import { IReview } from '../types/review';

export interface IThemeContext {
  user?: IUser;
  review: {
    items: IReview[];
    count: number;
    score: number;
  };
}
const ThemeContext = React.createContext<IThemeContext>({
  user: undefined,
  review: {
    items: [],
    count: 0,
    score: 0,
  },
});

export default ThemeContext;
