/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react';
import './styles/all.scss';
import { Switch, Route, Redirect } from 'react-router-dom';
import Nav from './components/Nav/Nav';
import Gallery from './pages/Gallery/Gallery';
import Footer from './components/Footer/Footer';
import axios from 'axios';
import ThemeContext, { IThemeContext } from './theme.context';
import Contact from './pages/Contact/Contact';
import Spinner from './components/Spinner/Spinner';
import Album from './pages/Album/Album';
import About from './pages/About/About';

interface ThemeProps {
  website: string;
  urlPrefix?: string;
}
const Theme: React.FunctionComponent<ThemeProps> = ({
  website,
  urlPrefix = '',
}) => {
  const [contextValue, setContextValue] = useState<IThemeContext>({
    user: undefined,
    review: {
      count: 0,
      items: [],
      score: 0,
    },
  });

  const [loading, setLoading] = useState<boolean>(false);

  useEffect(() => {
    fetchWebsiteData();
  }, [website]);

  useEffect(() => {
    fetchReviewsData(contextValue.user?._id);
  }, [contextValue.user]);

  async function fetchWebsiteData() {
    try {
      setLoading(true);
      const params = { website };
      const serverResponse = await axios.get(
        'https://api-staging.flayr.me/v1/website',
        { params }
      );
      setLoading(false);
      setContextValue(
        Object.assign({}, contextValue, { user: serverResponse.data })
      );
    } catch (error) {
      setLoading(false);

      console.log(error);
    }
  }

  async function fetchReviewsData(userId?: string) {
    if (userId) {
      setLoading(true);
      const serverResponse = await axios.get(
        'https://api-staging.flayr.me/v1/website/reviews/' + userId
      );
      setLoading(false);

      let review = {
        count: serverResponse.data.count,
        items: serverResponse.data.reviews,
        score: serverResponse.data.avgReviewScore,
      };
      setContextValue({
        ...contextValue,
        review,
      });
    }
  }

  return (
    <ThemeContext.Provider value={contextValue}>
      <div className="text-black langtang">
        <Nav
          logo={contextValue.user?.profileImg}
          albums={contextValue.user?.albums}
          user={contextValue.user}
          urlPrefix={urlPrefix}
        />

        <Switch>
          <Redirect exact path={`${urlPrefix}/`} to={`${urlPrefix}/gallery`} />
          <Route exact path={`${urlPrefix}/gallery`} component={Gallery} />
          <Route exact path={`${urlPrefix}/album/:albumId`} component={Album} />
          <Route exact path={`${urlPrefix}/about`} component={About} />
          <Route exact path={`${urlPrefix}/contact`} component={Contact} />
        </Switch>

        <Footer user={contextValue.user} />
      </div>
      <Spinner visible={loading} />
    </ThemeContext.Provider>
  );
};
export default Theme;
