/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import ThemeContext from '../../theme.context';
import Collage from '../../components/Collage/Collage';
import Testimonials from '../../components/Testimonials/Testimonials';
import Kit from '../../components/Kit/Kit';
import { useParams, useHistory } from 'react-router';

interface AlbumProps {}

const Album: React.FunctionComponent<AlbumProps> = () => {
  const { albumId } = useParams();
  const history = useHistory();
  const { user } = React.useContext(ThemeContext);
  const [album, setAlbum] = React.useState<any>({});

  React.useEffect(() => {
    console.log('here');
    if (
      !user?.albums?.length ||
      !user?.albums?.some((al) => al._id === albumId)
    ) {
      // redirect to gallery if no albums
      history.push('/gallery');
    }

    const album = user?.albums?.find((al) => al._id === albumId);
    setAlbum(album);
  }, [albumId]);

  return (
    <div className="px-5 md:px-20 pt-5">
      <header className="px-5 md:px-40 text-center md:text-left py-5 md:py-20 flex items-center">
        <h1 className="font-heading text-4xl md:text-5xl">{album.name}</h1>
        <div className="ml-auto text-lg">⬇</div>
      </header>
      <Collage images={album?.images?.map((i: any) => i.src)} />
      <Testimonials />

      <Kit kit={user?.kit.map((k) => k.name) || []} />
    </div>
  );
};
export default Album;
